*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
}
.middle_section{
    background-color: rgba(12, 12, 12, 0.886);
    width: 100%;
    height: 100%;
    
}
.search1{
    width: 100%;
    padding:7px  10px;
}
.first{
    border-bottom: 2px solid rgb(35, 33, 33);
   
}
.search1 input{
    width: 100%;
    padding: 6px 8px;
    color: rgb(204, 190, 190);
    font-size: 16px;
    background-color: rgb(28, 26, 26);
    border: none;
    border-radius: 5px;
}

.second{
    margin: 7px 5px;
}
.second button{
width: 100%;
padding: 8px 10px;
color: white;
background-color: rgba(92, 85, 85, 0.447);
font-size: 20px;
text-align: left;
border: none;
border-radius: 5px;
}

.third{
    width: 98%;
