    display: flex;
    justify-content: space-between;
    padding: 5px 10px;
    color: gray;
}
.left_mid{
    color: gray;
    font-size: 17px;
    font-weight: 500;

}
.new{
    width: 20%;
    position: relative;
}
.new .btn button{
    padding: 5px 10px;
    background-color: rgb(215, 35, 179);
    border: none;
    font-size: 14px;
    border-radius: 15px;
    color: white;
    font-weight: 600;
}
.star{
    position: absolute;
    right: -2px;
    top: -10px;
    font-size: 18px;
    color: white;
}

